body,
html {
 height: 100%;
}

.container {
 width: 100%;
 height: 100%;
 background: url(../images/index.jpg);
 -webkit-background-size: cover;
 background-size: cover;
}
.header {
 overflow: hidden;
 padding-bottom: 100px;
 img {
     width: 140px;
     height: 140px;
     margin: 45px 30px 0 50px;
     box-shadow: 5px 5px 10px rgba(0,0,0,0.6);
 }
 p {
     color: #310000;
     font-size: 26px;
 }
 .user {
     font-size: 36px;
     color: #000;
     margin-bottom: 35px;
     margin-top: 55.00000000000001px;
 }
}
.center {
 text-align: center;
 .ad {
     text-align: center;
     color: #fff;
     font-size: 36px;
     span {
         font-size: 60px;
     }
 }
 .erwei {
     margin-top: 95px;
     margin-bottom: 45px;
     img {
         width: 385px;
         height: 385px;
     }
     @media(max-width:320px) and (max-height:480px) {
         margin-top: 30px;
         margin-bottom: 30px;
     }
 }
 .tip1 {
     color: #fff;
     font-size: 30px;
     margin-bottom: 5px;
 }
 .tip2 {
     color: #540101;
     font-size: 40px;
 }
 #div {
   color: #fff;
 }
}
